export function generateMockData() {
  return Array.from({ length: 12 }, () =>
    yAxis.split(' ').filter(item => item).slice(0, 10000).map((item, index) => { return { x: index, y: item } })
    // Array.from({ length: 10000 }, (_, index) => ({
    //   x: index + 1,  // 确保 x 是从 1 开始的正整数
    //   y: Math.floor(Math.random() * 21) - 10  // 生成 1 到 100 之间的正整数
    // }))
  );
}

